.modal_wrap{
    width: 100%;
    height: 100%;
    position: fixed;
    left: 0;top: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #00000067;
    /* border: 10px solid #ff0000; */
    z-index: 999;
}
.modal{
    position: absolute;
    width: 920px;
    border: 5px solid #ffffff;
}
.modal span.xx{
    position: absolute;
    right: 20px;
    top: 20px;
    font-size: 40px;
    background: #00000073;
    width: 50px;
    height: 50px;
    border-radius: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-bottom: 10px;
    cursor: pointer !important;
}
.modal img{
    width: 100%;
    height: 100%;
    transform: none !important;
}
.pp{
    /* border: 2px solid #ff0000; */
    padding: 10px;
    margin-top: -10px;
    position: relative;
    background: #000;
}
.modal .pp h2{
    position: absolute;
    left: 50px;
    top: -250px;
    font-size: 52px;
    z-index: 1000;
    text-shadow: 0 0 16px #000;
}
.modal .pp .overview{
    position: absolute;
    left: 50px;
    top: -130px;
    font-size: 16px;
    margin-left: 20px;
    margin-right: 50px;
    z-index: 1000;
    text-shadow: 0 0 16px #000;
}
.yyy{
    display: flex;
    gap: 30px;
}