.benner{
    width: 100%;
    height: 100vh;
    object-fit: contain;
    box-sizing: border-box;
    /* border: 5px solid #ff0000; */
    position: relative;
    z-index: 0;
}
.benner .contents{
    position: absolute;
    left: 70px;
    bottom: 140px;
    text-shadow: 0 0 16px #333;
}
.benner .contents h1{
    font-size: 60px;
    text-shadow: 0 0 16px #333;
}
.benner .contents .description{
    width: 720px;
    line-height: 1.5;
    font-size: 20px;
    font-weight: bold;
}
.benner .contents .btn_wrap{
    display: flex;
    gap: 30px;  
    justify-content: flex-start;
    align-items: center;
}
.benner .contents .btn_wrap .play{
    padding: 15px 35px ;
    background: #e2e2e2;
    color: #141414;
    border-radius: 8px;
    font-size: 18px;
    font-weight: bold;
    margin: 20px 0;
    transition: all 0.2s ease-in-out;
}
.benner .contents .btn_wrap .info{
    padding: 15px 35px ;
    background: #80808069;
    color: #ffffff;
    border-radius: 8px;
    font-size: 18px;
    font-weight: bold;
    margin: 20px 0;
    transition: all 0.2s ease-in-out;
}
.benner .contents .btn_wrap .play:hover{
    background: #000000;
    color: #ffffff;
    
}
.benner .contents .btn_wrap .info:hover{
    background: #bbbbbb77;
    color: #ffffff;
}
