.header{
  position: fixed;
  left: 0;top: 0;
  width: 100%;
  height: 70px;
  padding: 15px 40px;
  /* border: 1px solid #ffffff; */
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 10;
  /* border-bottom: 1px solid #000000; */
}
.header h1{
  margin-top: 10px;
}
.header .wrap{
  display: flex;
  gap: 50px;  
  align-items: center;
}
.header .wrap ul.gnb{
  display: flex;
  gap: 40px;
  align-items: center;
}
.header .wrap ul.gnb li{
  color: #fff;
  cursor: pointer;
  text-shadow: 0 0 20px #000;
}
.header .wrap h1 img{
  width: 150px;
  height: 35px;
}
.header .right_box{
  display: flex;
  gap: 30px;
  align-items: center;
}
.header .right_box .avata{
  margin-top: 5px;
  border-radius: 8px;
}
.header .right_box div img{
  width: 32px;
  height: 32px;
  list-style: 75px;
  border-radius: 8px;
}
.list_wrap{
  display: flex;
  gap: 50px;
  width: 1200px;
  margin: 0 auto;
  margin-bottom: 50px;
}
.list_wrap ul li{
  color: #fff;
  line-height: 3;
}
.brand{
  margin-right: 20px;
}
.footerContents{
  width: 1200px;
  /* border: 1px solid #ffffff; */
  padding: 30px 10px;
}
.header-black{
  background: #000;
  transition: all 0.5s ease-in-out;
}
